.darkBG {
  background-color: rgba(0, 0, 0, 0.458);
  width: 100vw;
  height: 100vh;
  z-index: 0;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  position: absolute;
}

.centered {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.modal {
  width: 400px;
  height: 350px;
  background: #140536;
  color: white;
  z-index: 10;
  border-radius: 16px;
  box-shadow: 0 5px 20px 0 rgba(0, 0, 0, 0.04);
  border: 2px solid white;
}

.modalHeader {
  height: 75px;

  overflow: hidden;
  display: flex;
  align-items: center;

  border-top-left-radius: 16px;
  border-top-right-radius: 16px;
}

.heading {
  margin: 0;
  padding: 10px;
  color: white;
  font-weight: 500;
  font-size: 30px;
  text-align: center;
}

.modalContent {
  padding: 10px 10px 0px;
  text-align: left;
}

.label{
    font-family: Montserrat;
    font-style: normal;
    font-weight:400;
    font-size:24px;
    color: rgb(202, 202, 202);
    text-align: left;
    display:block;
}

.input{
    font-family: Montserrat;
    font-style: normal;
    font-weight:400;
    font-size:20px;
    background-color:#393158;
    color: white;
    border:1px solid #fff;
    width: calc(100% - 10px);
    height: 28px;
    margin:16px 0px 20px;
    border-radius: 4px;
}
.modalActions {
  padding:10px;
  text-align: left;
}


.closeBtn {
  cursor: pointer;
  font-weight: 500;
  padding: 4px 8px;
  border-radius: 8px;
  border: none;
  font-size: 18px;
  color: #2c3e50;
  background: white;
  transition: all 0.25s ease;
  box-shadow: 0 5px 20px 0 rgba(0, 0, 0, 0.06);
  position: absolute;
  right: 0;
  top: 0;
  align-self: flex-end;
  margin-top: -7px;
  margin-right: -7px;
}

.closeBtn:hover {
  box-shadow: 0 5px 20px 0 rgba(0, 0, 0, 0.04);
  transform: translate(-4px, 4px);
}

.submitBtn {
    width: calc(100% - 4px);
    height: 32px;
    border-radius: 4px;
    outline: none;
    border:none;
    background-color:#009cda;
    color: #fff;
    font-family: Montserrat;
    font-style: normal;
    font-weight: 500;
    font-size:18px;
}

.submitBtn:hover{
    background-color:#008ec6
}
